t:150%;font-family:宋体; letter-spacing:.1pt">评标</span></b><b><span style="font-size:12.0pt;line-height: 150%;font-family:宋体">办法前附表</span></b></p> <table style="width:100%;padding:16px;border-collapse:collapse;border:none" width="750" cellspacing="0" cellpadding="0" border="1"> <tbody><tr style="height:20.85pt"><td style="width:36.0pt;border:solid windowtext 1.0pt;padding:0cm 0cm 0cm 0cm; height:20.85pt" width="60"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:15.6pt;text-autospace:none" align="center"><b><span style="font-size:11.0pt; font-family:宋体">条款号</span></b></p> </td><td style="width:54.0pt;border:solid windowtext 1.0pt;border-left: none;padding:0cm 0cm 0cm 0cm;height:20.85pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:15.6pt;text-autospace:none" align="center"><b><span style="font-size:11.0pt; font-family:宋体">条款名称</span></b></p> </td><td style="width:360.0pt;border:solid windowtext 1.0pt;border-left: none;padding:0cm 0cm 0cm 0cm;height:20.85pt" width="600"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:15.1pt;text-autospace:none" align="center"><b><span style="font-size:11.0pt; font-family:宋体">评审因素与评审标准</span></b></p> </td></tr> <tr style="height:70.4pt"><td style="width:36.0pt;border:solid windowtext 1.0pt;border-top: none;padding:0cm 0cm 0cm 0cm;height:70.4pt" width="60"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:center;line-height:22.0pt" align="center"><span style="font-family:宋体">1</span></p> </td><td style="width:54.0pt;border-top:none;border-left:none;border-bottom: solid windowtext 1.0pt;border-right:solid windowtext 1.0pt;padding:0cm 0cm 0cm 0cm; height:70.4pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:center;line-height:22.0pt" align="center"><span style="font-family:宋体">评标办法</span></p> </td><td style="width:360.0pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:70.4pt" width="600"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';line-height:22.0pt"><span style="font-family:宋体">本次评标采用综合评估法<span>I</span>。评标委员会对满足招标文件实质性要求的投标文件，按照本章第<span>2</span>条规定的评分标准进行打分，并按得分由高到低顺序推荐中标候选人。</span></p> </td></tr> <tr style="height:343.1pt"><td style="width:36.0pt;border:solid windowtext 1.0pt;border-top: none;padding:0cm 0cm 0cm 0cm;height:343.1pt" width="60"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:14.85pt;text-autospace:none" align="center"><span style="font-size: 12.0pt;font-family:宋体">2.2</span></p> </td><td style="width:54.0pt;border-top:none;border-left:none;border-bottom: solid windowtext 1.0pt;border-right:solid windowtext 1.0pt;padding:0cm 0cm 0cm 0cm; height:343.1pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:16.05pt;text-autospace:none" align="center"><span style="font-family:宋体">第一信封资格审查</span></p> </td><td style="width:360.0pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:343.1pt" width="600"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(1)</span><span style="font-family:宋体">投标人具备有效的营业执照和基本账户开户许可证；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(2)</span><span style="font-family:宋体">投标人的资质证书有效且等级符合第二章“投标人须知前附表”附录<span>1</span>的规定：</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:21.0pt"><span style="font-family:宋体">(3)</span><span style="font-family:宋体">投标人的业绩符合第二章“投标人须知前附表”附录<span>2</span>的规定；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:21.1pt"><b><span style="font-family: 宋体">所附网页屏幕打印资料与“全国公路建设市场信用信息管理系统”的核查结果一致，如网页屏幕打印资料与相关网页显示不一致或网页屏幕打印资料信息不满足“附录<span>2</span>资格审查条件（业绩最低条件）”规定，则该业绩将不予认可；</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">&nbsp;</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(4) </span><span style="font-family:宋体">投标人的信誉符合第二章“投标人须知前附表”附录<span>3</span>的规定； </span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:21.0pt;line-height:22.0pt"><span style="font-family:宋体">&nbsp;(5) </span><span style="font-family: 宋体">投标人的主要人员资格符合第二章“投标人须知前附表”附录<span>4</span>的规定：</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:21.1pt;line-height:22.0pt"><b><span style="font-family:宋体">所附项目负责人业绩的网页屏幕打印资料与“全国公路建设市场信用信息管理系统”的核查结果一致，如网页屏幕打印资料与相关网页显示不一致或网页屏幕打印资料信息不满足“附录<span>4</span>资格审查条件（主要人员最低要求）”规定，则该业绩招标人将不予认可。</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">&nbsp;</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">&nbsp;</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(6)</span><span style="font-family:宋体">投标人不存在第二章“投标人须知”第<span>1.4.3</span>项规定的情形；</span></p> </td></tr> <tr style="height:347.0pt"><td style="width:36.0pt;border:solid windowtext 1.0pt;border-top: none;padding:0cm 0cm 0cm 0cm;height:347.0pt" width="60"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:15.8pt;text-autospace:none" align="center"><span style="font-size: 12.0pt;font-family:宋体">2.3</span></p> </td><td style="width:54.0pt;border-top:none;border-left:none;border-bottom: solid windowtext 1.0pt;border-right:solid windowtext 1.0pt;padding:0cm 0cm 0cm 0cm; height:347.0pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:.55pt;text-align:center; line-height:16.0pt;text-autospace:none" align="center"><span style="font-family:宋体">第一信封</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:.45pt;text-align:center; line-height:16.55pt;text-autospace:none" align="center"><span style="font-family:宋体">初步评审</span></p> </td><td style="width:360.0pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:347.0pt" width="600"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.5pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(1)</span><span style="font-family:宋体">投标文件按照招标文件规定的格式、内容填写，字迹清晰可辨；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(2)</span><span style="font-family:宋体">投标文件上法定代表人或其授权代理人的签字、投标人的单位章齐全，符合招标文件规定；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(3)</span><span style="font-family:宋体">投标人按照第二章“投标人须知”第<span>3.4.1</span>项和第<span>3.4.2</span>项规定的金额、形式、时间和账户等要求提供了投标保证金；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(4)</span><span style="font-family:宋体">投标人按照第二章“投标人须知”第<span>3.7.4</span>项的规定，提供了法定代表人的授权委托书或法定代表人身份证明，并附有公证机关出具的加盖钢印、单位章并盖有公证员签名章的公证书，钢印应清晰可辨，同时公证内容完全满足招标文件规定；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(5)</span><span style="font-family:宋体">投标人未以联合体形式投标</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:5.65pt;line-height:14.0pt"><span style="font-family:宋体">(6)</span><span style="font-family:宋体">投标人未对本项目提出分包计划。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(7)</span><span style="font-family:宋体">投标文件载明的招标项目完成期限未超过招标文件规定的时限；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt;margin-bottom: 0cm;margin-left:.55pt;margin-bottom:.0001pt;line-height:15.0pt;text-autospace: none"><span style="font-family:宋体">(8)</span><span style="font-family:宋体">投标文件中未出现有关投标报价的内容；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:5.65pt"><span style="font-family:宋体">(9)</span><span style="font-family:宋体">投标文件没有</span><span style="font-family:宋体">对招标人的权利提出削弱性或限制性要求，没有对投标人的责任和义务提出实质性修改；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:5.65pt"><span style="font-family:宋体">(10)</span><span style="font-family:宋体">投标人没有相互串通投标、没有弄虚作假行为；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:5.65pt"><span style="font-family:宋体">(11)</span><span style="font-family:宋体">投标文件未附有招标人不能接受的条件；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:5.65pt"><span style="font-family:宋体">(12)</span><span style="font-family:宋体">与招标人不存在利害关系或存在利害关系但不影响招标公正性的法人、其他组织或者个人。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:5.65pt"><span style="font-family:宋体">(13)</span><span style="font-family:宋体">投标人单位负责人不是同一人或者不存在控股、管理关系的不同单位。<span>(14)</span>投标人名称和资质与全国公路建设市场信用信息管理系统生成的信息一致。</span></p> </td></tr> <tr style="height:271.15pt"><td style="width:36.0pt;border:solid windowtext 1.0pt;border-top: none;padding:0cm 0cm 0cm 0cm;height:271.15pt" width="60"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:center" align="center"><span style="font-size:12.0pt;font-family:宋体">2.5</span></p> </td><td style="width:54.0pt;border-top:none;border-left:none;border-bottom: solid windowtext 1.0pt;border-right:solid windowtext 1.0pt;padding:0cm 0cm 0cm 0cm; height:271.15pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:center" align="center"><span style="font-family:宋体">第一信封详细评审</span></p> </td><td style="width:360.0pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:271.15pt" width="600"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:18.45pt;margin-bottom: 0cm;margin-left:.4pt;margin-bottom:.0001pt;text-indent:89.25pt;line-height: 16.55pt;text-autospace:none;word-break:break-all"><span style="font-family: 宋体">评审因素<span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>评分值</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt;margin-bottom: 0cm;margin-left:.45pt;margin-bottom:.0001pt;line-height:18.0pt;text-autospace: none"><span style="font-family:宋体">&nbsp; (1)</span><span style="font-family:宋体">投标文件第一信封<span>(</span>商务文件<span>)</span>：</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt; margin-bottom:0cm;margin-left:.55pt;margin-bottom:.0001pt;text-align:left; line-height:19.4pt;text-autospace:none" align="left"><span style="font-family: 宋体">&nbsp; a</span><span style="font-family:宋体">．投标人与本项目相关的具体业绩<span>&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<u>&nbsp;&nbsp;20 </u></span>分</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt; margin-bottom:0cm;margin-left:.55pt;margin-bottom:.0001pt;text-align:left; line-height:18.7pt;text-autospace:none" align="left"><span style="font-family: 宋体">&nbsp; b</span><span style="font-family:宋体">．拟投入本项目的人员资格和能力<span>&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<u>&nbsp;&nbsp;15 </u></span>分</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.55pt; margin-bottom:0cm;margin-left:.55pt;margin-bottom:.0001pt;text-align:left; line-height:18.7pt;text-autospace:none" align="left"><span style="font-family: 宋体">&nbsp; c</span><span style="font-family:宋体">．投标人的信誉<span>&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<u>&nbsp;&nbsp;5&nbsp; </u></span>分</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;