indowtext 1.0pt;padding:0cm 0cm 0cm 0cm; height:133.8pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:center" align="center"><span style="font-family:宋体">第二信封</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:center" align="center"><span style="font-family:宋体">初步评审</span></p> </td><td style="width:360.0pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:133.8pt" width="600"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">（<span>1</span>）内层封套符合招标文件规定；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">（<span>2</span>）在报价函上填写了投标总价，投标总价不高于招标人公布的最高投标限价，且报价唯一；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">（<span>3</span>）未按投标函规定格式填写投标报价大小写；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">（<span>4</span>）第二信封（报价清单）按照招标文件规定的格式、内容填写，字迹清晰可辨，内容齐全完整；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">（<span>5</span>）第二信封（报价清单）中法定代表人或其授权代理人的签字、投标人的单位章齐全，符合招标文件规定；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">