0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:.45pt;text-indent:10.5pt;line-height: 17.75pt;text-autospace:none"><span style="font-family:宋体">h</span><span style="font-family:宋体">．</span></p> </td><td rowspan="3" style="width:54.0pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:31.4pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:16.7pt;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:11.85pt;text-autospace:none" align="center"><span style="font-family:宋体">后续服务的安排及保证措施</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.45pt; margin-bottom:0cm;margin-left:.45pt;margin-bottom:.0001pt;text-align:center; line-height:17.75pt;text-autospace:none" align="center"><span style="font-family: 宋体">&nbsp;</span></p> </td><td rowspan="3" style="width:36.35pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:31.4pt" width="61"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-right:.45pt;text-indent:10.5pt;line-height: 18.7pt;text-autospace:none"><span style="font-family:宋体">10</span><span style="font-family:宋体">分</span></p> </td><td colspan="3" style="width:323.6pt;border-top:none;border-left: none;border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:31.4pt" width="539"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left" align="left"><span style="font-family: 宋体">投标人对于后续服务的承诺及保证措施安排一般，得<span>6</span>分；</span></p> </td></tr> <tr style="height:26.1pt"><td colspan="3" style="width:323.6pt;border-top:none;border-left: none;border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:26.1pt" width="539"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left" align="left"><span style="font-family: 宋体">投标人对于后续服务的承诺及保证措施安排较完善，得<span>6</span>－<span>8</span>分；</span></p> </td></tr> <tr style="height:32.15pt"><td colspan="3" style="width:323.6pt;border-top:none;border-left: none;border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:32.15pt" width="539"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left" align="left"><span style="font-family: 宋体">投标人对于后续服务的承诺及保证措施安排完善合理，得<span>8</span>－<span>10</span>分。</span></p> </td></tr> <tr style="height:30.7pt"><td rowspan="3" style="width:27.05pt;border:solid windowtext 1.0pt; border-top:none;padding:0cm 0cm 0cm 0cm;height:30.7pt" width="45"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.1pt; margin-bottom:0cm;margin-left:.1pt;margin-bottom:.0001pt;text-align:center; line-height:9.6pt;text-autospace:none" align="center"><span style="font-family: 宋体">i</span></p> </td><td rowspan="3" style="width:54.0pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:30.7pt" width="90"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:center;line-height:18.0pt; text-autospace:none" align="center"><span style="font-family:宋体">投标价</span></p> </td><td rowspan="3" style="width:36.35pt;border-top:none;border-left:none; border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:30.7pt" width="61"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';margin-top:0cm;margin-right:.1pt; margin-bottom:0cm;margin-left:.1pt;margin-bottom:.0001pt;text-align:center; line-height:9.6pt;text-autospace:none" align="center"><span style="font-family: 宋体">10</span><span style="font-family:宋体">分</span></p> </td><td colspan="3" style="width:323.6pt;border-top:none;border-left: none;border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:30.7pt" width="539"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left" align="left"><span style="font-family: 宋体">投标价的确定<span>:</span>投标价＝报价函文字报价</span></p> </td></tr> <tr style="height:128.35pt"><td colspan="3" style="width:323.6pt;border-top:none;border-left: none;border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:128.35pt" width="539"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">评标基准价的确定<span>:</span>按第一信封（商务及技术文件）评审得分由高到低的顺序，对投标人的第二信封（报价清单）通过初步评审及算术性修正后的前三名投标人投标价作算术平均，将该平均值作为评标基准价；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">&nbsp;&nbsp;&nbsp; &nbsp;</span><span style="font-family:宋体">如果两个或两个以上投标人第一个信封（商务及技术文件）得分相同，则以评分内容的序号在前者得分高者优先，如果各分项得分均相同，则由评标委员会投票决定次序。</span></p> </td></tr> <tr style="height:93.35pt"><td colspan="3" style="width:323.6pt;border-top:none;border-left: none;border-bottom:solid windowtext 1.0pt;border-right:solid windowtext 1.0pt; padding:0cm 0cm 0cm 0cm;height:93.35pt" width="539"> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">投标价得分计算公式示例：</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">（<span>1</span>）如果投标人的投标价＞评标基准价，则投标价得分<span>=F-</span>（投标人投标价<span>-</span>评标基准价）<span>/</span>评标基准价×<span>100</span>×<span>E1</span>；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span style="font-family:宋体">（<span>2</span>）如果投标人的投标价≤评标基准价，则投标价得分<span>=F+(</span>投标人投标价<span>-</span>评标基准价<span>)/</span>评标基准价×<span>100</span>×<span>E2</span>。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:22.5pt"><span style="font-family:宋体">其中，<span>F=10</span>；<span>E1=0.5</span>；<span>E2=0.3</span>；投标价最低得分为<span>0</span>分。</span></p> </td></tr> <tr height="0"><td style="border:none" width="45"></td><td style="border:none" width="90"></td><td style="border:none" width="61"></td><td style="border:none" width="134"></td><td style="border:none" width="15"></td><td style="border:none" width="390"></td></tr> </tbody></table> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:21.0pt;layout-grid-mode:char"><span style="font-family:宋体">各评审因素得分应以评标委员会各成员的打分平均值确定，该平均值以去掉一个最高和一个最低分后计算。各项得分、评标基准价、投标价得分均保留<span>2</span>位小数。</span></p> <span style="font-size:12.0pt;line-height:150%;font-family:宋体"><br style="page-break-before:always" clear="all"> </span> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';line-height:150%;layout-grid-mode:char"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing: .1pt">1.</span></b><b><span style="font-size:12.0pt;line-height:150%; font-family:宋体;letter-spacing:.1pt">总则</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">本次评标采用综合评估法<span>I</span>。评标委员会对满足招标文件实质性要求的投标文件，按照本章第<span>2</span>条规定的评分标准进行打分，并按得分由高到低顺序推荐中标候选人，或根据招标人授权直接确定中标人。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';line-height:150%;page-break-after:avoid;layout-grid-mode: char"><b><span style="font-size:12.0pt;line-height:150%;font-family: 宋体;letter-spacing:.1pt">2</span></b><b><span style="font-size:12.0pt; line-height:150%;font-family:宋体;letter-spacing:.1pt">．评标程序和评审标准</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>1&nbsp; </span>评标程序</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标工作按以下程序进行：</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; 2</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">．<span>1</span>．<span>1&nbsp; </span>第一信封资格审查；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">第一信封初步评审；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">第一信封澄清<span>(</span>如果需要<span>)</span>；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">第一信封详细评审。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp; &nbsp;&nbsp;2</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>1</span>．<span>2&nbsp; </span>第二信封初步评审；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">第二信封算术性修正；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">第二信封澄清<span>(</span>如果需要<span>)</span>；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">第二信封详细评审。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp; &nbsp;&nbsp;2</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>1</span>．<span>3&nbsp; </span>综合评价，推荐中标候选人。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp; &nbsp;&nbsp;2</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>1</span>．<span>4&nbsp; </span>编写评标报告。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>2&nbsp; </span>第一信封资格审查</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会首先对投标人提交的资格审查表进行审查，有一项不符合评审标准的，其投标文件被否决。通过资格审查的标准见“评标办法前附表”。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会可以要求投标人提交第二章“投标人须知”第<span>3</span>．<span>5</span>．<span>1</span>项规定的有关证明和证件的原件，以便核验。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>3&nbsp; </span>第一信封初步评审</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会对通过资格审查的投标文件第一信封<span>(</span>商务及技术文件<span>)</span>进行初步评审，有一项不符合评审标准的，其投标文件被否决。通过初步评审的标准见“评标办法前附表”。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>4&nbsp; </span>第一信封澄清</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">在评标过程中，评标委员会可以书面形式要求投标人对所提交投标文件中含义不明确、对同类问题表述不一致或者有明显文字错误的内容作必要的澄清、说明或者补正。评标委员会不接受投标人主动提出的澄清、说明或补正。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">澄清、说明或者补正应以书面方式进行，并不得超出投标文件的范围或者改变投标文件的实质性内容。投标人的书面澄清、说明和补正属于投标文件的组成部分。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会对投标人提交的澄清、说明或补正有疑问的，可以要求投标人进一步澄清、说明或补正，直至满足评标委员会的要求。凡超出招标文件规定的或给发包人带来未曾要求的利益的变化、偏差或其他因素在评标时不予考虑。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>5&nbsp; </span>第一信封详细评审</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会只对通过初步评审的投标文件第一信封<span>(</span>商务及技术文件<span>)</span>进行详细评审。评标委员会按“评标办法前附表”规定的评审因素和评分值进行评分，并计算出各投标人的商务和技术得分。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>6&nbsp; </span>第二信封开标</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;text-indent:24.6pt; line-height:150%" align="left"><span style="font-size:12.0pt;line-height:150%; font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">第一信封<span>(</span>商务及技术文件<span>)</span>评审结束后，招标人将按照第二章“投标人须知”第<span>5</span>．<span>1</span>款规定的时间和地点对通过投标文件第一信封<span>(</span>商务及技术文件<span>)</span>评审的投标文件第二信封<span>(</span>报价清单<span>)</span>进行开标。</span><span style="font-size:12.0pt;line-height:150%; font-family:宋体">通过第一信封初步评审、资格审查的投标人少于<span>3</span>名的，评标委员会可以否决全部投标；未否决全部投标的，评标委员会应当在评标报告中阐明理由，开启其报价文件信封，但评标委员会在进行财务建议书评审时仍有权否决全部投标；评标委员会未在报价文件评审时否决全部投标的，应当在评标报告中阐明理由并推荐中标候选人。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>7&nbsp; </span>第二信封初步评审</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会对通过投标文件第一信封<span>(</span>商务及技术文件<span>)</span>评审的投标文件第二信封<span>(</span>报价清单<span>)</span>进行初步评审，有一项不符合评审标准的，其投标文件被否决。通过初步评审的标准见“评标办法前附表”。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>8&nbsp; </span>第二信封算术性修正</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会对通过投标文件第二信封<span>(</span>报价清单<span>)</span>初步评审的投标人的投标报价进行校核，并对其中的算术性错误予以修正。修正的原则如下：</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; (1)</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">大写金额与小写金额不一致的，以大写金额为准；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; (2)</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">单价金额与数量相乘与合价金额不一致的，以单价金额为准；如果单价金额有明显的小数点位置差错，应以标出的合价金额为准，同时对单价金额予以修正；</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; (3)</span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">合价金额累计与总价金额不一致的，以合价金额为准，修正总价金额。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>9&nbsp; </span>第二信封澄清</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">算术性修正后的报价如果与投标人原报价不同，评标时将书面通知投标人进行澄清，投标人应确认算术性修正后的报价；如投标人拒绝确认，则其投标文件将不予评审，其投标文件被否决，同时没收其投标担保。修正后的最终投标报价仅作为签订合同的一个依据，不参与投标价得分的计算。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">修正后的最终投标报价若超过最高投标限价<span>(</span>如有<span>)</span>，投标人的投标文件被否决。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>10&nbsp; </span>第二信封详细评审</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:24.0pt;line-height:150%"><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">计算所有通过第二信封<span>(</span>报价清单<span>)</span>初步评审以及算术性修正后的投标人的投标价得分。投标价得分的计算方法见“评标办法前附表”。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-indent:24.0pt;line-height:150%"><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing: .15pt">&nbsp;</span><span style="font-size:12.0pt;line-height:150%;font-family: 宋体;letter-spacing:.15pt">评标委员会在对报价文件进行评审后，有效投标不足<span>3</span>个使得投标明显缺乏竞争的，可以否决全部投标。未否决全部投标的，评标委员会应当在评标报告中阐明理由并推荐中标候选人。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>11&nbsp; </span>评标排序</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会成员应当按照评标办法的规定，独立评分并署名。各投标人的综合得分为商务和技术得分与报价得分之和。按照综合得分由高到低的顺序，评标委员会对投标人进行排名。如最终得分相同时，则投标文件第一信封<span>(</span>商务及技术文件<span>)</span>得分较高的优先。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">2</span></b><b><span style="font-size:12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">．<span>12&nbsp; </span>评标结果</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';text-align:left;line-height:150%; layout-grid-mode:char;text-autospace:none" align="left"><span style="font-size: 12.0pt;line-height:150%;font-family:宋体;letter-spacing:.15pt">&nbsp;&nbsp;&nbsp; </span><span style="font-size:12.0pt;line-height:150%;font-family:宋体; letter-spacing:.15pt">评标委员会应当在评标工作完成后，按“评标办法前附表”规定的人数推荐中标候选人并向招标人提交书面评标报告。</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman';line-height:150%;text-autospace:none"><b><span style="font-size:26.0pt;line-height:150%;font-family:宋体;letter-spacing: 1.3pt">&nbsp;</span></b></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span>&nbsp;</span></p> <p style="margin:0cm;margin-bottom:.0001pt;text-align:justify;text-justify:inter-ideograph;font-size:10.5pt;font-family:'Times New Roman'"><span>&nbsp;</span></p> </div>`,
				// 记录本次找到 table 的位置
				prevNum: 0,
			};
		},
		// 页面挂载
		onLoad() {
			this.findTableStyle();
		},
		methods: {

			findTableStyle() {
				// 找到table
				const strartIndex = this.xml.indexOf('<table', this.prevNum);
				this.prevNum = strartIndex + 1
				// 阻止递归
				if (strartIndex === -1) return;
				const endIndex = this.xml.indexOf('>', strartIndex + 1);
				// 截取前 + table前置标签 + 截取后
				this.xml = this.changeStr(this.xml, strartIndex, endIndex,
					'<table style="width:100%;border-collapse:collapse;border:none;margin-bottom: 20px;"');
				// 递归
				this.findTableStyle();
			},
			changeStr(string, strat, end, changeStr) {
				return string.substr(0, strat) + changeStr + string.substr(end, string.length);
			}
		}
	};
</script>

<style lang="stylus">
	.content{
		 div{
			 width:100% !important;
		 }
	}
</style>
